'use client';

import { useEffect, useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Textarea } from '@/components/ui/textarea';
import { Alert, AlertDescription } from '@/components/ui/alert';

interface PandiRecord {
  domain: string;
  domainId?: string;
  status?: string;
  createdDate?: string;
  lastUpdated?: string;
  expiryDate?: string;
  organization?: string;
  address?: string;
  city?: string;
  province?: string;
  postalCode?: string;
  country?: string;
  phone?: string;
  email?: string;
  nameServers?: string[];
}

export default function PandiScraperPage() {
  const [records, setRecords] = useState<PandiRecord[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  const [filterAcId, setFilterAcId] = useState<boolean>(true);
  const [filterDesaId, setFilterDesaId] = useState<boolean>(true);
  const [customDomains, setCustomDomains] = useState<string>('');
  const [activeTab, setActiveTab] = useState<string>('predefined');

  const fetchPredefinedDomains = async (filter: string, demo: boolean = false) => {
    setLoading(true);
    setError(null);
    try {
      const url = `/api/scrape-pandi?filter=${filter}${demo ? '&demo=true' : ''}`;
      const res = await fetch(url);
      const json = await res.json();
      if (json.success) {
        setRecords(json.data);
        if (json.demoMode) {
          setError(`Demo Mode: ${json.message || 'Showing sample data for demonstration purposes'}`);
        }
      } else {
        throw new Error(json.error || 'Unknown error occurred.');
      }
    } catch (err: any) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  const fetchCustomDomains = async (demo: boolean = false) => {
    if (!customDomains.trim()) {
      setError('Please enter at least one domain.');
      return;
    }

    setLoading(true);
    setError(null);
    try {
      const domainList = customDomains
        .split('\n')
        .map(d => d.trim())
        .filter(d => d.length > 0);

      const res = await fetch('/api/scrape-pandi', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ domains: domainList, demo }),
      });

      const json = await res.json();
      if (json.success) {
        setRecords(json.data);
        if (json.demoMode) {
          setError(`Demo Mode: ${json.message || 'Showing sample data for demonstration purposes'}`);
        }
      } else {
        throw new Error(json.error || 'Unknown error occurred.');
      }
    } catch (err: any) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  const handlePredefinedSearch = (demo: boolean = false) => {
    let filter = 'all';
    if (filterAcId && !filterDesaId) {
      filter = 'ac.id';
    } else if (!filterAcId && filterDesaId) {
      filter = 'desa.id';
    }
    fetchPredefinedDomains(filter, demo);
  };

  // Filter records based on selected criteria for display
  const filteredRecords = records.filter(record => {
    if (!filterAcId && !filterDesaId) return false;
    let include = false;
    if (filterAcId && record.domain.endsWith('.ac.id')) {
      include = true;
    }
    if (filterDesaId && record.domain.endsWith('.desa.id')) {
      include = true;
    }
    return include;
  });

  const getStatusColor = (status?: string) => {
    switch (status?.toLowerCase()) {
      case 'available':
        return 'bg-green-100 text-green-800 border-green-200';
      case 'registered':
        return 'bg-blue-100 text-blue-800 border-blue-200';
      default:
        return 'bg-gray-100 text-gray-800 border-gray-200';
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100">
      <div className="max-w-7xl mx-auto px-4 py-8">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-gray-900 mb-4">
            PANDI Domain Scraper
          </h1>
          <p className="text-lg text-gray-600 max-w-2xl mx-auto">
            Search and analyze Indonesian domains (.ac.id and .desa.id) from the PANDI database. 
            Check domain availability and get detailed WHOIS information.
          </p>
        </div>

        <Tabs value={activeTab} onValueChange={setActiveTab} className="mb-8">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="predefined">Predefined Patterns</TabsTrigger>
            <TabsTrigger value="custom">Custom Domains</TabsTrigger>
          </TabsList>

          <TabsContent value="predefined" className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle>Search Predefined Domain Patterns</CardTitle>
                <CardDescription>
                  Search common university (.ac.id) and village (.desa.id) domain patterns
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="flex flex-wrap gap-4">
                  <label className="flex items-center space-x-2">
                    <input
                      type="checkbox"
                      checked={filterAcId}
                      onChange={() => setFilterAcId(!filterAcId)}
                      className="w-4 h-4 text-blue-600 rounded focus:ring-blue-500"
                    />
                    <span className="text-sm font-medium">.ac.id domains</span>
                  </label>
                  <label className="flex items-center space-x-2">
                    <input
                      type="checkbox"
                      checked={filterDesaId}
                      onChange={() => setFilterDesaId(!filterDesaId)}
                      className="w-4 h-4 text-blue-600 rounded focus:ring-blue-500"
                    />
                    <span className="text-sm font-medium">.desa.id domains</span>
                  </label>
                </div>
                <div className="flex flex-wrap gap-2">
                  <Button 
                    onClick={() => handlePredefinedSearch(false)}
                    disabled={loading || (!filterAcId && !filterDesaId)}
                    className="flex-1 sm:flex-none"
                  >
                    {loading ? 'Searching...' : 'Search Live'}
                  </Button>
                  <Button 
                    onClick={() => handlePredefinedSearch(true)}
                    disabled={loading || (!filterAcId && !filterDesaId)}
                    variant="outline"
                    className="flex-1 sm:flex-none"
                  >
                    {loading ? 'Loading...' : 'Try Demo'}
                  </Button>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="custom" className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle>Search Custom Domains</CardTitle>
                <CardDescription>
                  Enter your own list of .ac.id and .desa.id domains to check (one per line)
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <Textarea
                  placeholder="ui.ac.id&#10;itb.ac.id&#10;sukamaju.desa.id&#10;makmur.desa.id"
                  value={customDomains}
                  onChange={(e) => setCustomDomains(e.target.value)}
                  rows={6}
                  className="font-mono text-sm"
                />
                <div className="flex flex-wrap gap-2">
                  <Button 
                    onClick={() => fetchCustomDomains(false)}
                    disabled={loading || !customDomains.trim()}
                    className="flex-1 sm:flex-none"
                  >
                    {loading ? 'Searching...' : 'Search Live'}
                  </Button>
                  <Button 
                    onClick={() => fetchCustomDomains(true)}
                    disabled={loading || !customDomains.trim()}
                    variant="outline"
                    className="flex-1 sm:flex-none"
                  >
                    {loading ? 'Loading...' : 'Try Demo'}
                  </Button>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>

        {/* Loading State */}
        {loading && (
          <div className="text-center py-8">
            <div className="inline-flex items-center space-x-2">
              <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600"></div>
              <span className="text-gray-600">Searching domains...</span>
            </div>
          </div>
        )}

        {/* Error/Info State */}
        {error && (
          <Alert className={`mb-6 ${error.includes('Demo Mode') ? 'border-blue-200 bg-blue-50' : 'border-red-200 bg-red-50'}`}>
            <AlertDescription className={error.includes('Demo Mode') ? 'text-blue-800' : 'text-red-800'}>
              {error.includes('Demo Mode') ? (
                <div className="flex items-center space-x-2">
                  <span className="text-blue-600">ℹ️</span>
                  <span>{error}</span>
                </div>
              ) : (
                <span>Error: {error}</span>
              )}
            </AlertDescription>
          </Alert>
        )}

        {/* Results */}
        {!loading && !error && records.length > 0 && (
          <div className="space-y-6">
            <div className="flex items-center justify-between">
              <h2 className="text-2xl font-semibold text-gray-900">
                Search Results ({filteredRecords.length} domains)
              </h2>
              <div className="text-sm text-gray-500">
                Showing {activeTab === 'predefined' ? 'predefined patterns' : 'custom domains'}
              </div>
            </div>

            {filteredRecords.length === 0 ? (
              <div className="text-center py-8 text-gray-500">
                No domains match your current filter settings.
              </div>
            ) : (
              <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
                {filteredRecords.map((record, index) => (
                  <Card key={index} className="hover:shadow-lg transition-shadow">
                    <CardHeader className="pb-3">
                      <div className="flex items-center justify-between">
                        <CardTitle className="text-lg font-semibold text-gray-900">
                          {record.domain}
                        </CardTitle>
                        <Badge className={getStatusColor(record.status)}>
                          {record.status || 'Unknown'}
                        </Badge>
                      </div>
                    </CardHeader>
                    <CardContent className="space-y-2">
                      {record.domainId && (
                        <div className="text-sm">
                          <span className="font-medium text-gray-600">ID:</span>
                          <span className="ml-2 text-gray-900">{record.domainId}</span>
                        </div>
                      )}
                      {record.organization && (
                        <div className="text-sm">
                          <span className="font-medium text-gray-600">Organization:</span>
                          <span className="ml-2 text-gray-900">{record.organization}</span>
                        </div>
                      )}
                      {record.createdDate && (
                        <div className="text-sm">
                          <span className="font-medium text-gray-600">Created:</span>
                          <span className="ml-2 text-gray-900">{record.createdDate}</span>
                        </div>
                      )}
                      {record.expiryDate && (
                        <div className="text-sm">
                          <span className="font-medium text-gray-600">Expires:</span>
                          <span className="ml-2 text-gray-900">{record.expiryDate}</span>
                        </div>
                      )}
                      {record.city && record.province && (
                        <div className="text-sm">
                          <span className="font-medium text-gray-600">Location:</span>
                          <span className="ml-2 text-gray-900">{record.city}, {record.province}</span>
                        </div>
                      )}
                      {record.email && (
                        <div className="text-sm">
                          <span className="font-medium text-gray-600">Email:</span>
                          <span className="ml-2 text-gray-900">{record.email}</span>
                        </div>
                      )}
                      {record.nameServers && record.nameServers.length > 0 && (
                        <div className="text-sm">
                          <span className="font-medium text-gray-600">Name Servers:</span>
                          <div className="ml-2 text-gray-900 text-xs">
                            {record.nameServers.slice(0, 2).map((ns, i) => (
                              <div key={i}>{ns}</div>
                            ))}
                            {record.nameServers.length > 2 && (
                              <div className="text-gray-500">+{record.nameServers.length - 2} more</div>
                            )}
                          </div>
                        </div>
                      )}
                    </CardContent>
                  </Card>
                ))}
              </div>
            )}
          </div>
        )}

        {!loading && !error && records.length === 0 && (
          <div className="text-center py-12">
            <div className="text-gray-500 text-lg">
              No results yet. Use the search options above to find domains.
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
